const merchData = [
  {
    title: 'Smith Monkey Business T-Shirt',
    price: '29.99',
    image: '<img src="images/monkey.PNG" style="width:10%" />',
  },
  {
    title: 'Smith Monkey Business T-Shirt',
    price: '29.99',
    image: '<img src="images/Jack_Karakashian_Headshot.png" style="width:60%" />',
  },
];

const cardHolderMerch = document.getElementById('card-holder-merch');
for (let i = 0; i < merchData.length; i += 1) {
  const merch = merchData[i];

  const cardMerch = document.createElement('div');
  cardMerch.setAttribute('class', 'card');
  cardMerch.innerHTML = `
    <h2>${merch.image}</h2>
    <p>${merch.title}</p>
    <p>${merch.price}</p>
  `;

  // Add the cream background color to the card
  cardMerch.style.backgroundColor = '#fdecd7';
  cardMerch.style.padding = '10px';
  cardMerch.style.marginBottom = '20px';
  cardMerch.style.marginLeft = '275px'; // Adjusted the margin to move the cards 100px to the right

  cardHolderMerch.appendChild(cardMerch);
}
